/* --------------------------------
 Primary style
 -------------------------------- */

.nucleo-icons-page-styles *,
.nucleo-icons-page-styles *::after,
.nucleo-icons-page-styles *::before {
    box-sizing: border-box;
}

html.nucleo-icons-page-styles {
    font-size: 62.5%;
}

.nucleo-icons-page-styles body {
    font-size: 1.6rem;
    font-family: sans-serif;
    color: #333;
    background: #efefef;
}

.nucleo-icons-page-styles a {
    color: #766acf;
    text-decoration: none;
}


/* --------------------------------
 Main components
 -------------------------------- */

.nucleo-icons-page-styles header {
    text-align: center;
    padding: 120px 0 0;
}

.nucleo-icons-page-styles header h1 {
    font-size: 2rem;
}

.nucleo-icons-page-styles header p {
    font-size: 1.4rem;
    margin-top: 0.6em;
}

.nucleo-icons-page-styles header a:hover {
    text-decoration: underline;
}

.nucleo-icons-page-styles section {
    width: 90%;
    max-width: 1200px;
    margin: 50px auto;
}

.nucleo-icons-page-styles section h2 {
    border-bottom: 1px solid #e2e2e2;
    padding: 0 0 1em 0.2em;
    margin-bottom: 1em;
}

.nucleo-icons-page-styles ul::after {
    clear: both;
    content: "";
    display: table;
}

.nucleo-icons-page-styles ul li {
    width: 25%;
    float: left;
    padding: 16px 0;
    text-align: center;
    border-radius: 0.25em;
    -webkit-transition: background 0.2s;
    -moz-transition: background 0.2s;
    transition: background 0.2s;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    overflow: hidden;
}

.nucleo-icons-page-styles ul li:hover {
    background: #dbdbdb;
}

.nucleo-icons-page-styles ul p,
.nucleo-icons-page-styles ul em,
.nucleo-icons-page-styles ul input {
    display: inline-block;
    font-size: 1rem;
    color: #999;
    -webkit-user-select: auto;
    -moz-user-select: auto;
    -ms-user-select: auto;
    user-select: auto;
    white-space: nowrap;
    width: 100%;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}

.nucleo-icons-page-styles ul p {
    padding: 10px;
    margin-top: 10px;
}

.nucleo-icons-page-styles ul p::selection,
.nucleo-icons-page-styles ul em::selection {
    background: #766acf;
    color: #efefef;
}

.nucleo-icons-page-styles ul em {
    margin-bottom: 8px;
}

.nucleo-icons-page-styles ul em::before {
    content: '[';
}

.nucleo-icons-page-styles ul em::after {
    content: ']';
}

.nucleo-icons-page-styles ul input {
    text-align: center;
    background: transparent;
    border: none;
    box-shadow: none;
    outline: none;
}

@media only screen and (min-width: 768px) {
    .nucleo-icons-page-styles ul li {
        width: 20%;
        float: left;
    }
}

@media only screen and (min-width: 1024px) {
    .nucleo-icons-page-styles ul li {
        width: 12.5%;
        float: left;
        padding: 32px 0;
    }
}


/* --------------------------------
 Smart fixed navigation
 -------------------------------- */

.nucleo-icons-page-styles {
    /* mobile first */
}

.nucleo-icons-page-styles #cd-nav ul {
    position: fixed;
    width: 90%;
    max-width: 400px;
    max-height: 90%;
    right: 5%;
    bottom: 5%;
    border-radius: 0.25em;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    background: white;
    visibility: hidden;
    /* remove overflow:hidden if you want to create a drop-down menu - but then remember to fix/eliminate the list items animation */
    overflow: hidden;
    overflow-y: auto;
    z-index: 1;
    /* Force Hardware Acceleration in WebKit */
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transform: scale(0);
    -moz-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    -webkit-transform-origin: 100% 100%;
    -moz-transform-origin: 100% 100%;
    -ms-transform-origin: 100% 100%;
    -o-transform-origin: 100% 100%;
    transform-origin: 100% 100%;
    -webkit-transition: -webkit-transform 0.3s, visibility 0s 0.3s;
    -moz-transition: -moz-transform 0.3s, visibility 0s 0.3s;
    transition: transform 0.3s, visibility 0s 0.3s;
}

.nucleo-icons-page-styles #cd-nav ul li {
    /* Force Hardware Acceleration in WebKit */
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    display: block;
    width: 100%;
    padding: 0;
    text-align: left;
}

.nucleo-icons-page-styles #cd-nav ul li:hover {
    background-color: #fff;
}

.nucleo-icons-page-styles #cd-nav ul.is-visible {
    visibility: visible;
    -webkit-transform: scale(1);
    -moz-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
    -webkit-transition: -webkit-transform 0.3s, visibility 0s 0s;
    -moz-transition: -moz-transform 0.3s, visibility 0s 0s;
    transition: transform 0.3s, visibility 0s 0s;
}

.nucleo-icons-page-styles #cd-nav li a {
    display: block;
    padding: 1em;
    font-size: 1.2rem;
    border-bottom: 1px solid #eff2f6;
}

.nucleo-icons-page-styles #cd-nav li a:hover {
    color: #333;
}

.nucleo-icons-page-styles #cd-nav li:last-child a {
    border-bottom: none;
}

.nucleo-icons-page-styles .cd-nav-trigger {
    position: fixed;
    bottom: 5%;
    right: 5%;
    width: 44px;
    height: 44px;
    background: white;
    border-radius: 0.25em;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
    /* image replacement */
    overflow: hidden;
    text-indent: 100%;
    white-space: nowrap;
    z-index: 2;
}

.nucleo-icons-page-styles .cd-nav-trigger span {
    /* the span element is used to create the menu icon */
    position: absolute;
    display: block;
    width: 20px;
    height: 2px;
    background: #333;
    top: 50%;
    margin-top: -1px;
    left: 50%;
    margin-left: -10px;
    -webkit-transition: background 0.3s;
    -moz-transition: background 0.3s;
    transition: background 0.3s;
}

.nucleo-icons-page-styles .cd-nav-trigger span::before,
.nucleo-icons-page-styles .cd-nav-trigger span::after {
    content: '';
    position: absolute;
    left: 0;
    background: inherit;
    width: 100%;
    height: 100%;
    /* Force Hardware Acceleration in WebKit */
    -webkit-transform: translateZ(0);
    -moz-transform: translateZ(0);
    -ms-transform: translateZ(0);
    -o-transform: translateZ(0);
    transform: translateZ(0);
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    -webkit-transition: -webkit-transform 0.3s, background 0s;
    -moz-transition: -moz-transform 0.3s, background 0s;
    transition: transform 0.3s, background 0s;
}

.nucleo-icons-page-styles .cd-nav-trigger span::before {
    top: -6px;
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -ms-transform: rotate(0);
    -o-transform: rotate(0);
    transform: rotate(0);
}

.nucleo-icons-page-styles .cd-nav-trigger span::after {
    bottom: -6px;
    -webkit-transform: rotate(0);
    -moz-transform: rotate(0);
    -ms-transform: rotate(0);
    -o-transform: rotate(0);
    transform: rotate(0);
}

.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open {
    box-shadow: none;
}

.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span {
    background: rgba(232, 74, 100, 0);
}

.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::before,
.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::after {
    background: #333;
}

.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::before {
    top: 0;
    -webkit-transform: rotate(135deg);
    -moz-transform: rotate(135deg);
    -ms-transform: rotate(135deg);
    -o-transform: rotate(135deg);
    transform: rotate(135deg);
}

.nucleo-icons-page-styles .cd-nav-trigger.menu-is-open span::after {
    bottom: 0;
    -webkit-transform: rotate(225deg);
    -moz-transform: rotate(225deg);
    -ms-transform: rotate(225deg);
    -o-transform: rotate(225deg);
    transform: rotate(225deg);
}

.nucleo-icons-page-styles .now-ui-icons {
    display: inline-block;
    font: normal normal normal 32px/1 'Nucleo Outline';
    speak: none;
    text-transform: none;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}